.container {
    display: inline-flex;
    // align-items: center;
    position: relative;
    cursor: pointer;
    margin-right: 20px;
    align-items: center;
}
.inputRadio {
    position: absolute;
    opacity: 0;
    top: -1px;
    left: -3px;
}
.circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: solid 1px #d7d9db;
    position: relative;
    margin-right: 8px;
    transition: all ease-in-out .2s;
    &::after {
        content: '';
        display: inline-block;
        width: 9px;
        height: 9px;
        top: 3px;
        left: 3px;
        border-radius: 50%;
        background-color: transparent;
        position: absolute;
       transition: all ease-in-out .2s;
    }
}

.active {
    .circle {
        border-color: #00b831;
        &::after {
            background-color: #00b831;
        }
    }
}
.active.disable {
    .circle { 
        border-color: #d7d9db;
        background-color: #f0f0f5;
        &::after {
            background-color: #d7d9db;
        }
    }
}
.disable {
    cursor: not-allowed;
    .circle { 
        border-color: #d7d9db;
        background-color: #f0f0f5;
        &::after {
            // background-color: #d7d9db;
        }
    }
    
}

.vertical {
    display: flex;
    flex-direction: column;
    .container {
        margin-bottom: 10px;
        :last-child {
            margin: 0;
        }
    }
}